"use client";

import { useMemo, useState } from "react";
import { Heart, ShoppingCart } from "lucide-react";
import { Card, CardContent } from "@/components/ui/card";
import FillterCollection from "./FillterCollection";
import { Button } from "../ui/button";
import { useDispatch } from "react-redux";
import { fetchProductsByCategory } from "@/redux/slices/product/read";

// افترضنا أن عندك بيانات المنتجات
import { products } from "@/lib/data/products"; // لو موجود خارجيًا

export default function ProductCollectionPage() {
  const [selectedCategory, setSelectedCategory] = useState<string | null>(null);
  const [selectedSize, setSelectedSize] = useState<string | null>(null);
  const [selectedDiscount, setSelectedDiscount] = useState<number | null>(null);

  const [currentPage, setCurrentPage] = useState(1);
  const itemsPerPage = 8;

  const filteredProducts = useMemo(() => {
    return products.filter((product) => {
      const matchCategory = selectedCategory ? product.category === selectedCategory : true;
      const matchSize = selectedSize ? product.size === selectedSize : true;
      const matchDiscount = selectedDiscount ? product.price <= selectedDiscount : true;

      return matchCategory && matchSize && matchDiscount;
    });
  }, [selectedCategory, selectedSize, selectedDiscount]);

  const totalPages = Math.ceil(filteredProducts.length / itemsPerPage);

  const paginatedProducts = useMemo(() => {
    const start = (currentPage - 1) * itemsPerPage;
    return filteredProducts.slice(start, start + itemsPerPage);
  }, [filteredProducts, currentPage]);

  const handleResetPage = () => {
    setCurrentPage(1);
  };

  return (
    <div className="flex flex-col md:flex-row">
      {/* الفلاتر */}
      <aside className="w-full md:w-1/4 p-4 sticky top-20 h-fit border-r border-gray-200 bg-white z-10">
        <FillterCollection
          onCategoryChange={(value) => {
            setSelectedCategory(value);
            handleResetPage();
          }}
          onSizeChange={(value) => {
            setSelectedSize(value);
            handleResetPage();
          }}
          onDiscountChange={(value) => {
            setSelectedDiscount(value);
            handleResetPage();
          }}
        />
      </aside>

      {/* المنتجات */}
      <main className="w-full md:w-3/4 p-4 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
        {paginatedProducts.map((product, i) => (
          <Card key={i}>
            <CardContent className="p-4 flex flex-col items-center">
              <img src={product.image} alt={product.name} className="w-full h-40 object-cover rounded" />
              <h3 className="mt-2 font-semibold text-center">{product.name}</h3>
              <p className="text-sm text-gray-600">السعر: {product.price} جنيه</p>
            </CardContent>
          </Card>
        ))}
      </main>

      {/* Pagination */}
      <div className="w-full p-4 flex justify-center mt-4">
        <div className="flex gap-2">
          {Array.from({ length: totalPages }, (_, idx) => (
            <Button
              key={idx}
              variant={currentPage === idx + 1 ? "default" : "outline"}
              onClick={() => setCurrentPage(idx + 1)}
            >
              {idx + 1}
            </Button>
          ))}
        </div>
      </div>
    </div>
  );
}
